.gallery {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.imageContainer img {
  width: 95vw;
  height: auto;
}

.closeButton {
  position: absolute;
  top: 10rem;
  right: 1rem;
}

.nextButton {
  position: absolute;
  z-index: 5;
  right: 1rem;
}

.prevButton {
  position: absolute;
  z-index: 5;
  left: 1rem;
}

.prevButton > button,
.nextButton > button {
  background-color: rgba(0, 0, 0, 0.5);
}

.loadedImage {
  display: block;
}

.unloadedImage {
  display: none;
}

@media only screen and (device-width: 768px) {
  .imageContainer {
    min-width: 80vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .imageContainer img {
    width: 80vw;
    height: auto;
  }
}

@media only screen and (min-width: 769px) {
  .imageContainer {
    min-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .imageContainer img {
    width: auto;
    height: 80vh;
  }
  .closeButton {
    top: 4rem;
    right: 2rem;
  }
  .nextButton,
  .prevButton {
    position: relative;
  }
}

/* Portrait */
@media only screen and (min-width: 1024px) and (max-height: 1366px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 1.5) {
  .imageContainer img {
    width: 80vw;
    height: auto;
  }
}

/* Landscape */
@media only screen and (min-width: 1024px) and (max-height: 1366px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 1.5) {
  .imageContainer img {
    width: 80vw;
    height: auto;
  }
}
